.cont {

  bottom: 10%;
  max-width: 600px;
  margin: 0 auto;
  margin-top: 20px;
  padding: 20px;
  background-color: transparent;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

h1 {
  text-align: center;
  position: relative;
}

form {
  
  max-width: 500px;
  margin: 0 auto;
}

label {
  display: block;
  margin-bottom: 5px;
}

.name,
.email,
textarea {
  text-transform: none;
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  
}

input[type="submit"] {
  
  width: 100%;
  padding: 10px;
  background: linear-gradient(to right, #ff105f, #ffad06);
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: all 8s;
  
}

input[type="submit"]:hover {
  background: linear-gradient(to left, #ff105f, #ffad06);
}
